body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  font-family: 'Arial Black', sans-serif;
  font-family: 'Arial Light', sans-serif;
  font-family: 'Arial CE', sans-serif;
  font-family: 'Arial CE MT Black', sans-serif;
  font-size: 12px;
  line-height: 1.2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  justify-content: center;
}

.App {
  width: 793px;
  text-align: justify;
}

.d-flex {
  display: flex;
}

.align-items-center {
  align-items: center;
}

.align-items-end {
  align-items: flex-end;
}

.flex-column {
  flex-direction: column;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-end {
  justify-content: end;
}

.gap-0-1 {
  gap: 0 1rem;
}

.gap-0-2 {
	gap: 0 2rem;
}

.gap-0-3 {
  gap: 0 3rem;
}

.flex-wrap {
  flex-wrap: wrap;
}

.small {
	font-size: smaller;
}

.fw-500 {
  font-weight: 500;
}

.fw-600 {
	font-weight: 600;
}

.fw-bold {
  font-weight: bold;
}

.link {
  color: inherit;
  text-decoration: none;
}

.text-muted {
  color: #3f3f3f;
}

.text-nowrap {
  white-space: nowrap;
}

.text-break {
  word-wrap: break-word;
  word-break: break-word;
}

.w-70 {
  width: 70%;
}

.w-30 {
  width: 30%;
}

.mb-2 {
  margin-bottom: 0.2rem;
}

.mb-5 {
  margin-bottom: 0.5rem;
}

h1 {
  font-size: 28px;
  margin: 0;
}

h2 {
  font-weight: 600;
	text-align: center;
	border-top: 1px solid #dadff2;
	border-bottom: 1px solid #959baf;
	padding: 0.5rem 0 0.3rem 0;
	margin: 0.5rem 0 0.3rem 0;
}

h3 {
	margin: 0;
}

h4 {
  margin-top: 0.2rem;
  margin-bottom: 0.1rem;
}

svg {
  width: 12px !important;
  height: 12px !important;
  margin-left: 0.3rem;
}

#summary {
  color: #3f3f3f;
  margin: 5px 0;
}

.designation {
	font-size: 14px;
  font-weight: 500;
}